.contact-page {
  margin-bottom: 120px;
  text-align: center;
}

.contact-page__container {
  padding: 0 6.6%;
}

.contact-page__exp {
  font-size: 1.6rem;
}

.contact-page__mail-btn {
  margin: 0 auto;
  margin-top: 45px;
  width: 450px;
  line-height: 78px;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 1.6rem;
  font-weight: 600;
  border: 1px solid #1c1f22;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-page .mail-img {
  width: 30px;
  display: flex;
  align-items: center;
  margin-right: 25px;
}

.contact-page__mail {
  display: inline-block;
  background-size: 24px auto;
}

.contact-page__formarea {
  max-width: 1080px;
  margin: 0 auto;
  margin-top: 70px;
}

.contact-page__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 10px;
}

.contact-page__item label {
  font-weight: 500;
}

.contact-page__item label span {
  color: red;
  margin-left: 5px;
}

.contact-page__item select, .contact-page__item input, .contact-page__item textarea {
  background: #f5f6f7;
  padding: 0 30px;
  width: 830px;
  height: 90px;
  font-size: 1.6rem;
  text-align: left;
  position: relative;
}

.contact-page__item select:focus, .contact-page__item input:focus, .contact-page__item textarea:focus {
  border: 1px solid orange;
}

.contact-page__item textarea {
  height: 290px;
  padding-top: 15px;
}

.contact-page__btn {
  margin-top: 20px;
  color: white;
  background: black;
  width: 460px;
  height: 70px;
}

.select {
  position: relative;
}

.select::after {
  position: absolute;
  display: block;
  content: "";
  height: 18px;
  width: 18px;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  border-top: 1px solid black;
  border-right: 1px solid black;
  transform: rotate(135deg);
  z-index: 1000;
}
